import React from 'react';

export const BlogFilter = () => {
    return (
        <form className="blog__filter">
            <div className="blog__search">
                <label>
                    <input type="search" name="search" placeholder="" required/>
                </label>
                <button className="blog__search--submit" type="button" title="">
                    <svg width="17" height="17" viewBox="0 0 17 17">
                        <use xlinkHref="#search"></use>
                    </svg>
                </button>
            </div>
            <div className="blog__sort">
                <span className="blog__sort--name"></span>
                <div className="blog__sort--select">
                    <select name="sort" className="custom-select">
                        <option value="rating" data-html="<span> </span>"> </option>
                        <option value="date" data-html="<span> </span>"> </option>
                        <option value="popularity" data-html="<span> </span>"> </option>
                        <option value="alphabet" data-html="<span>  </span>">  
                        </option>
                    </select>
                </div>
            </div>
        </form>
    );
}
